@charset "UTF-8";
:root {
  --white-color: #fff;
  --bg-color: #081016;
  --cyan-color: #0dd3d1;
  --gold-color: #ffd700;
  --max-width-container: calc(1254px + 30px); }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Black.woff2") format("woff2");
  font-weight: 900;
  font-display: swap;
