.btn {
  padding: 15px 20px;
  background-color: transparent;
  color: var(--white-color);
  text-transform: uppercase;
  font-size: 21px;
  font-weight: 900;
  position: relative; }
  .btn span::before,
  .btn span::after {
    content: "";
    position: absolute;
    background-color: var(--white-color);
    box-shadow: 0 0 3px 1px var(--gold-color), 0 0 7px 1px var(--gold-color), 0 0 15px 0px var(--gold-color);
    transition: all .5s ease; }
  .btn span:first-child::before {
    top: 0;
    left: 0;
    height: 2px;
    width: 50%; }
  .btn span:first-child::after {
    top: 0;
    left: 0;
    height: 50%;
    width: 2px; }
  .btn span:last-child::before {
    bottom: 0;
    right: 0;
    height: 2px;
    width: 50%; }
  .btn span:last-child::after {
    bottom: 0;
    right: 0;
    height: 50%;
    width: 2px; }

.btn:hover span:first-child::before,
.btn:hover span:last-child::before {
  width: 100%; }

.btn:hover span:first-child::after,
.btn:hover span:last-child::after {
  height: 100%; }

.title {
  margin: 0;
  margin-left: 45px;
  margin-bottom: 60px;
  display: inline-block;
  position: relative;
  font-weight: 700;
  font-size: 35px;
  text-transform: uppercase; }
  .title::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: var(--white-color);
    box-shadow: 0 0 3px 1px var(--white-color), 0 0 5px 1px var(--white-color); }

.item-links {
  width: 40px;
  height: 40px; }

.item-links:not(:last-child) {
  margin-right: 40px; }

.links svg {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  fill: var(--white-color);
  border-radius: 50%;
  transition: transform .5s ease; }

.item-links svg:hover {
  transform: scale(1.2);
  box-shadow: 0 0 7px 1px var(--gold-color), 0 0 15px 1px var(--gold-color); }

.header {
  left: 0;
  right: 0;
  position: fixed;
  padding: 20px 0;
