.hero {
  height: var(--vh);
  min-height: 500px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center; }
  .hero__video-content {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    overflow: hidden;
    background-color: var(--bg-color);
    line-height: 0;
    font-size: 0;
    object-fit: cover; }
    .hero__video-content::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 5;
      background-color: rgba(0, 0, 0, 0.4); }
    .hero__video-content::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      width: 100%;
      height: 200px;
      z-index: 10;
      background: white;
      background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #081016 90%); }
  .hero__video {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-height: 100%;
    min-width: 100%;
    object-fit: cover;
    margin: -1px; }
  .hero__inner {
    z-index: 20;
    color: #fff;
    text-align: center; }
  .hero__title {
    font-size: 60px;
    font-weight: 900;
    margin: 0 0 60px; }
  .hero__text-descr {
