    transform: translateX(-50%); }

.about {
  padding: 70px 0 70px; }
  .about__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 0 0 -60px; }
  .about__item {
    flex: 0 0 30%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 60px; }
  .about__item:nth-child(odd) .about__img {
    animation: neon-one 5s linear infinite; }
  .about__item:nth-child(even) .about__img {
    animation: neon-two 5s linear infinite; }
  .about__img {
    box-sizing: content-box;
    width: 65px;
    height: 65px;
