.works {
  padding: 70px 0 150px; }
  .works__item:not(:last-child) {
    margin-bottom: 200px; }
  .works__box {
    display: flex;
    justify-content: space-between;
    align-items: flex-start; }
  .works__text-box {
    max-width: 480px;
    margin-left: 50px; }
  .works__img-box {
    position: relative;
    margin-left: 45px; }
  .works__img-big-box {
    position: relative; }
    .works__img-big-box::after {
      content: " ";
      position: absolute;
      top: 50%;
      left: 50%;
      width: 105%;
      height: 110%;
      transform: translate(-50%, -50%);
      border: 3px solid var(--white-color);
      box-shadow: 0 0 3px 0 var(--white-color) inset, 0 0 5px 1px var(--white-color) inset, 0 0 3px 0 var(--white-color), 0 0 5px 1px var(--white-color); }
  .works__img-middle-box, .works__img-small-box {
    position: absolute;
    z-index: 5;
