.form {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .form__input, .form__textarea {
    width: 370px;
    padding: 15px 25px;
    margin-bottom: 20px;
    border-radius: 10px;
    color: var(--white-color);
    font-family: "Computer", sans-serif;
    font-size: 20px;
    background-color: transparent;
    border: 3px solid var(--white-color); }
  .form__input::placeholder, .form__textarea::placeholder {
    font-weight: 700;
    font-size: 17px;
    color: #999; }
  .form__textarea {
    resize: none; }
  .form__submit {
    font-family: "Computer", sans-serif;
    font-size: 25px;
    font-weight: 900;
    transition: transform .5s ease;
    color: var(--white-color);
    padding: 5px; }
    .form__submit:hover {
      transform: scale(1.2); }
  .form__label {
