.slider {
  max-width: 700px;
  background-image: url("../../img/retro-computer.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  padding-top: 135px;
  padding-bottom: 300px; }
  .slider__slide {
